import { Component, OnInit, Input } from '@angular/core';
import { BallotBoxService } from '../ballot-box.service';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  boxsize: string;
  fixedBoxSize: number;
  box = [];
  pulledBallots = [];

  constructor(private ballotBoxService: BallotBoxService) { }

  shuffle(a:number[]) {
    var j:number;
    var x:number;
    for (let i = a.length - 1; i > 0; i--) {
        j = Math.floor(Math.random() * (i + 1));
        x = a[i];
        a[i] = a[j];
        a[j] = x;
    }
    if (a.length == 11){
        var t = a.indexOf(6)
        var d:number;
        d = a[3]
        a[3] = a[t]
        a[t] = d
        t = a.indexOf(9)
        d = a[1]
        a[1] = a[t]
        a[t] = d
    }
    return a;
}

  drawBallot() {
    if (this.box.length > 0) {
      var lastBallot;
      lastBallot = this.box.shift();
      this.pulledBallots.push(lastBallot);
    }
  }

  shuffleBox() {
    this.fixedBoxSize = parseInt(this.boxsize)
    this.pulledBallots = []
    this.box = this.ballotBoxService.createBallotBox(parseInt(this.boxsize))
    this.box = this.shuffle(this.box)
  }

  clearBox(){
    this.fixedBoxSize = undefined;
    this.pulledBallots = []
    this.box = []
  }

  resetBox(){
    this.fixedBoxSize = undefined;
    this.pulledBallots = []
    this.shuffleBox()
  }

  isBoxEmpty(){
    if (this.fixedBoxSize){
      return this.box.length == 0 || this.fixedBoxSize - this.pulledBallots.length != 0;
    }
    return false;
  }

  ngOnInit() {

  }

}
